import { mapState,mapMutations,mapGetters } from "vuex"
export default{
	props:{
		isShou:Boolean
	},
	methods:{
		...mapMutations(['change']),
		goCart(){
			if(this.loginStatus){
				uni.switchTab({
					url:"../../pages/cart/cart"
				})
			}else{
				uni.navigateTo({
					url:"../../pages/login/login"
				})
			}
			
		}
	},
	computed:{
		...mapGetters(['getGoods']),
		...mapState({
			loginStatus:state=>state.user.loginStatus
		})
	}
}
